$color-row1: red;

.container-1{
    display: flex;
}
.box{
   width: 80px;
   height: 100px;
   text-align: center;
 
}
.base-color {
  background-color: $color-row1;
}

.darken-1{
    background-color: darken($color-row1, 10%);
}

.darken-2{
    background-color: darken($color-row1, 20%);
}

.darken-3{
    background-color: darken($color-row1, 30%);
}

.darken-4{
    background-color: darken($color-row1, 40%);
}